import { ethers } from 'ethers';
/**
 *  token  0x1234 => 12.34
 * @param bigNumber 
 * @param decimals  
 * @returns string
 */
export function bigNumberToFixed(bigNumber, decimals) {
    if (!bigNumber) {
        return 0;
    }
    var str = bigNumber.toString();
    var len = str.length;
    if (len < decimals - 3) {
        return 0;
    }
    if (len > decimals) {
        var header = str.substring(0, len - decimals);
        var body = str.substring(len - decimals, len - decimals + 4 > len ? len - decimals + 4 : len);
        var result_1 = "".concat(header, ".").concat(body);
        return +result_1;
    }
    var step = decimals - len;
    var result = '0.';
    for (var i = 0; i < step; i++) {
        result += ('0');
    }
    var tail = str.substring(0, 4 - step);
    result = result.concat(tail);
    return +result;
}
/**
 *  token   12.34 => 0x1234
 * @param fixed 
 * @param decimals 
 * @returns BigNumber
 */
export function fixedToBigNumber(fixed, decimals) {
    if (!fixed) {
        return ethers.constants.Zero;
    }
    var strs = fixed.toString().split('.');
    var power = 0;
    var str = strs[0];
    if (strs.length > 1) {
        var tail = strs[1];
        var len = tail.length;
        if (len > decimals) {
            tail = tail.substring(0, decimals);
        }
        str += tail;
        power = tail.length;
    }
    var ten = ethers.BigNumber.from(10);
    var times = ten.pow(power);
    var timesStr = ethers.BigNumber.from(str);
    return timesStr.mul(ten.pow(decimals)).div(times);
}
